import classes from "./css/destination.module.css";
import { useState } from "react";

import { destinations } from "../../data.json";

function Destination() {
  const [currentDestination, setCurrentDestination] = useState(destinations[0]);

  return (
    <section className={classes.section}>
      <div className={classes.info}>
        <div className={classes.title}>
          <h5>
            <b>01</b> Pick your destination
          </h5>
          <img src={currentDestination.images.png} alt="planet" />
        </div>
        <div className={classes.details}>
          <ul className={classes.ul}>
            {destinations.map((destination) => (
              <li
                className={
                  currentDestination === destination ? classes.active : null
                }
                onClick={() => setCurrentDestination(destination)}
              >
                {destination.name}
              </li>
            ))}
          </ul>
          <div className={classes.description}>
            <h2>{currentDestination.name}</h2>
            <span>{currentDestination.description}</span>
          </div>
          <div className={classes.metrics}>
            <div>
              <h6>Distance</h6>
              <p>{currentDestination.distance}</p>
            </div>
            <div>
              <h6>Time</h6>
              <p>{currentDestination.travel}</p>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
}

export default Destination;
