import { Link } from "react-router-dom";
import { useLocation } from "react-router-dom";
import { useState } from "react";

import logo from "../../assets/shared/logo.svg";
import classes from "./css/nav.module.css";
import hamburger from "../../assets/shared/icon-hamburger.svg";
import close from "../../assets/shared/icon-close.svg";

function Navigation() {
  const location = useLocation();
  const { pathname } = location;
  const splitLocation = pathname.split("/");
  const [menu, setMenu] = useState(false);

  return (
    <header className={classes.header}>
      <div className={classes.logo}>
        <img src={logo} alt="logo"></img>
      </div>
      <div className={classes.line}></div>
      <img
        onClick={() => setMenu(!menu)}
        src={menu ? close : hamburger}
        className={classes.hamburger}
        alt="menu"
      />
      <nav className={`${classes.nav} ${menu ? classes.open : null}`}>
        <ul className={classes.ul}>
          <li className={splitLocation[1] === "Space-website-challenge" ? classes.active : null}>
            <Link onClick={() => setMenu(false)} to="/Space-website-challenge/">
              <b>00</b> Home
            </Link>
          </li>
          <li
            className={
              splitLocation[2] === "destination" ? classes.active : null
            }
          >
            <Link onClick={() => setMenu(false)} to="/Space-website-challenge/destination">
              <b>01</b> Destination
            </Link>
          </li>
          <li className={splitLocation[2] === "crew" ? classes.active : null}>
            <Link onClick={() => setMenu(false)} to="/Space-website-challenge/crew">
              <b>02</b> Crew
            </Link>
          </li>
          <li
            className={
              splitLocation[2] === "technology" ? classes.active : null
            }
          >
            <Link onClick={() => setMenu(false)} to="/Space-website-challenge/technology">
              <b>03</b> Technology
            </Link>
          </li>
        </ul>
      </nav>
    </header>
  );
}

export default Navigation;
