import classes from "./css/technology.module.css";

import { useState } from "react";
import { technology } from "../../data.json";

function Technology() {
  const [currentTech, setCurrentTech] = useState(technology[0]);
  return (
    <section className={classes.section}>
      <div className={classes.info}>
        <div className={classes.details}>
          <h5>
            <b>03</b>Space Launch 101
          </h5>
          <div className={classes.description}>
            <ul>
              {technology.map((tech, i) => (
                <li
                  className={currentTech === tech ? classes.active : null}
                  onClick={() => setCurrentTech(tech)}
                >
                  <span
                    className={currentTech === tech ? classes.active : null}
                  >
                    {i + 1}
                  </span>
                </li>
              ))}
            </ul>
            <div className={classes.detailedText}>
              <p>The Terminology...</p>
              <h3>{currentTech.name}</h3>
              <span>{currentTech.description}</span>
            </div>
          </div>
        </div>
        <div className={classes.image}>
          <picture>
            <source media="(min-width:1025px)" srcset={currentTech.images.portrait}/>
            <img src={currentTech.images.landscape} alt="technology" />
          </picture>
        </div>
      </div>
    </section>
  );
}

export default Technology;
