import classes from "./css/crew.module.css";

import { useState } from "react";

import { crew } from "../../data.json";

function Crew() {
  const [currentCrew, setCurrentCrew] = useState(crew[0]);
  return (
    <section className={classes.section}>
      <div className={classes.info}>
        <div className={classes.details}>
          <h5>
            <b>02</b>Meet your crew
          </h5>
          <div>
            <h4>{currentCrew.role}</h4>
            <h3>{currentCrew.name}</h3>
            <span>{currentCrew.bio}</span>
          </div>
          <ul className={classes.ul}>
            {crew.map((member) => (
              <li
                className={currentCrew === member ? classes.active : null}
                onClick={() => setCurrentCrew(member)}
              ></li>
            ))}
          </ul>
        </div>

        <img src={currentCrew.images.png} alt="crew member"></img>
      </div>
    </section>
  );
}

export default Crew;
